.main-form-container {
  position:absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

}
.main-form {
  color: white !important;
  border-radius: 0 !important;
  background-color: #111111 !important;
  height: 100vh;
}

@media screen and (max-width: 720px) {
  .bg-main {
    display: none !important;
  }
  .main-form {
    width: 100vw !important;
  }
  .main-form-container {
    padding: 0 !important;
    margin: 0 !important;
  }
}